.checklist-progress{
    margin-bottom: 6px;
    position: relative;
    .checklist-progress-percentage{
        color: $clr2;
    font-size: 11px;
    left: 0;
    line-height: 10px;
    position: absolute;
    text-align: center;
    top: -1px;
    width: 32px;
    }
    .checklist-progress-bar{
        background: #091e4214;
        border-radius: 4px;
        clear: both;
        height: 8px;
        margin: 0 0 0 40px;
        overflow: hidden;
        position: relative;
    }
    .checklist-progress-bar-current {
        background: #5ba4cf;
        bottom: 0;
        left: 0;
        position: absolute;
        top: 0;
        transition-duration: .14s;
        transition-property: width,background;
        transition-timing-function: ease-in;

        &.done{
            background: #61bd4f;
        }
    }
}
